"use client";

import React, { useEffect, useState } from "react";
import { motion, useMotionValue, useSpring } from "framer-motion";

export const CustomCursor = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [isPointer, setIsPointer] = useState(false);
  const [isText, setIsText] = useState(false);

  const cursorX = useMotionValue(-100);
  const cursorY = useMotionValue(-100);

  const springConfig = { damping: 25, stiffness: 700 };
  const springX = useSpring(cursorX, springConfig);
  const springY = useSpring(cursorY, springConfig);

  useEffect(() => {
    const moveCursor = (e: MouseEvent) => {
      cursorX.set(e.clientX);
      cursorY.set(e.clientY);
      setIsVisible(true);
    };

    const handleMouseEnter = () => setIsVisible(true);
    const handleMouseLeave = () => setIsVisible(false);

    const updateCursorType = (e: MouseEvent) => {
      const target = e.target as HTMLElement;

      // Check if cursor is over clickable elements
      const isClickable = target.matches('button, a, [role="button"]');
      setIsPointer(isClickable);

      // Check if cursor is over text elements
      const isTextElement = target.matches(
        'p, h1, h2, h3, h4, h5, h6, span, div:not([role="button"])'
      );
      setIsText(isTextElement);
    };

    window.addEventListener("mousemove", moveCursor);
    window.addEventListener("mouseenter", handleMouseEnter);
    window.addEventListener("mouseleave", handleMouseLeave);
    window.addEventListener("mouseover", updateCursorType);

    return () => {
      window.removeEventListener("mousemove", moveCursor);
      window.removeEventListener("mouseenter", handleMouseEnter);
      window.removeEventListener("mouseleave", handleMouseLeave);
      window.removeEventListener("mouseover", updateCursorType);
    };
  }, [cursorX, cursorY]);

  return (
    <>
      {/* Outer circle */}
      <motion.div
        className="fixed pointer-events-none z-[999]"
        style={{
          x: springX,
          y: springY,
          translateX: "-50%",
          translateY: "-50%",
        }}
      >
        <motion.div
          className="w-8 h-8 rounded-full border border-gray-400"
          initial={{ scale: 0, opacity: 0 }}
          animate={{
            scale: isVisible ? 1 : 0,
            opacity: isVisible ? 1 : 0,
            borderColor: isPointer ? "rgb(239 68 68)" : "rgb(156 163 175)",
          }}
          transition={{ duration: 0.2 }}
          style={{
            scale: isPointer ? 1.2 : isText ? 1.5 : 1,
          }}
        />
      </motion.div>

      {/* Inner dot */}
      <motion.div
        className="fixed pointer-events-none z-[999]"
        style={{
          x: springX,
          y: springY,
          translateX: "-50%",
          translateY: "-50%",
        }}
      >
        <motion.div
          className="w-1 h-1 rounded-full bg-gray-400"
          initial={{ scale: 0, opacity: 0 }}
          animate={{
            scale: isVisible ? 1 : 0,
            opacity: isVisible ? 1 : 0,
            backgroundColor: isPointer ? "rgb(239 68 68)" : "rgb(156 163 175)",
          }}
          transition={{ duration: 0.2 }}
        />
      </motion.div>
    </>
  );
};

export default CustomCursor;
